<template>
  <div>
    <div class="search_box" style="z-index: 2;">
      <div class="icon_box">
        <van-icon name="arrow-left" size="30px" @click="$toPage(-1)" />
      </div>
      <input
        placeholder="药品名、通用名、生产厂家"
        ref="search_input"
        type="search"
        @keyup.13="searchFn"
        v-model="keyword"
        @input="inputKeyword"
      />
      <div class="search_btn" @click="searchFn">
        搜索
      </div>
    </div>
    <div class="placeholder"></div>

    <!--历史搜索-->
    <div class="history_box" v-if="historyList.length > 0">
      <div class="history_box_title">
        <div>历史搜索</div>
        <div @click="clearHistory"><van-icon name="delete" size="20px" /></div>
      </div>
      <div class="history_box_content">
        <div
          v-for="(item, index) in historyList"
          :key="index"
          @click="selectKeyword(item)"
        >
          {{ item }}
        </div>
      </div>
    </div>
    <!--热门搜索-->
    <div class="history_box">
      <div class="history_box_title">
        <div>热门搜索</div>
        <!-- <div><van-icon name="delete" size="20px" /></div> -->
      </div>
      <div class="history_box_content">
        <div
          v-for="(item, index) in hotList"
          :key="index"
          @click="selectKeyword(item)"
        >
          {{ item }}
        </div>
      </div>
    </div>
    <!--关键词联想-->
    <div
      :class="
        showThink ? 'keyword_think keyword_think_activity' : 'keyword_think'
      "
    >
      <div
        v-for="(item, index) in thinkList"
        :key="index"
        @click="selectKeyword(item)"
      >
        {{ item }}
      </div>
    </div>
  </div>
</template>

<script>
import { Dialog } from "vant";
export default {
  data() {
    return {
      keyword: "",
      historyList: [], //历史搜索
      hotList: [
        "感冒用药",
        "五官用药",
        "心血管药",
        "三九/999 感冒灵颗粒",
        "心血管药"
      ], //热门搜索
      thinkList: [
        "感冒用药",
        "五官用药",
        "心血管药",
        "三九/999 感冒灵颗粒",
        "心血管药"
      ],
      showThink: false
    };
  },
  mounted() {
    //获取缓存中历史搜索
    this.historyList = JSON.parse(sessionStorage.getItem("historyList")) || [];

    this.keyword = this.$route.query.keyword;
    this.$refs.search_input.focus();
  },
  methods: {
    //清除历史搜索
    clearHistory() {
      Dialog.confirm({
        title: "提示",
        message: "您确定要删除搜索历史吗？"
      })
        .then(() => {
          // on confirm
          this.historyList = [];
          sessionStorage.setItem("historyList", this.historyList);
        })
        .catch(() => {
          // on cancel
        });
    },
    selectKeyword(keyword) {
      this.keyword = keyword;
      this.$refs.search_input.focus();
      this.inputKeyword();
    },
    inputKeyword() {
      // if(this.keyword != ''){
      //   this.showThink = true
      // }else{
      //   this.showThink = false
      // }
    },
    searchFn: function() {
      //缓存历史搜索
      let historyList = this.historyList;
      if (this.keyword != "") {
        historyList.unshift(this.keyword);
      }

      historyList = [...new Set(historyList)];
      if (historyList.length > 30) {
        historyList = historyList.slice(0, 30);
      }
      sessionStorage.setItem("historyList", JSON.stringify(historyList));
      if (this.keyword == '#') {
        this.$router.push({
          path: "/bdLogin"
        });
      } else {
        this.$router.push({
          path: "/classifi",
          query: {
            keyword: this.keyword
          }
        });
      }
    }
  }
};
</script>

<style scoped>
.search_box {
  background-color: #ffffff;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  height: 90px;
  display: flex;
  align-items: center;
}
.icon_box {
  height: 90px;
  width: 85px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #999999;
}
.search_box input {
  display: block;
  border-radius: 28px;
  height: 58px;
  border: none;
  flex: 1;
  background-color: #f0f0f0;
  padding-left: 50px;
  font-size: 28px;
}
.search_btn {
  width: 100px;
  text-align: center;
  line-height: 90px;
  font-size: 28px;
  color: #333333;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.placeholder {
  height: 90px;
}
.search_btn img {
  filter: invert(30%);
  width: 50px;
  height: 50px;
  display: block;
}
.search_btn div {
  position: absolute;
  top: 0;
  right: 30px;
  width: 14px;
  height: 14px;
  border-radius: 10px;
  background-color: #ff0000;
}
.history_box {
  margin: 20px;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
}
.history_box_title {
  display: flex;
  justify-content: space-between;
  font-size: 28px;
  height: 60px;
  line-height: 60px;
}
.history_box_title div {
  display: flex;
  align-items: center;
}
.history_box_content {
  line-height: 60px;
  display: flex;
  overflow: hidden;
  flex-wrap: wrap;
}
.history_box_content div {
  padding: 0 10px;
  height: 50px;
  line-height: 50px;
  margin-top: 10px;
  font-size: 24px;
  background-color: #f2f2f2;
  margin-right: 10px;
  border-radius: 6px;
  color: #999999;
}
.keyword_think {
  position: fixed;
  background-color: #ffffff;
  left: 0;
  right: 0;
  max-height: 600px;
  overflow: scroll;
  top: -600px;
  transition: top 0.5s;
}
.keyword_think_activity {
  top: 90px;
  transition: top 0.5s;
}
.keyword_think div {
  padding: 0 20px;
  font-size: 28px;
  height: 70px;
  line-height: 70px;
  color: #999999;
  border-bottom: 1px solid #eeeeee;
}
</style>
